/*-----FOOTER------*/

.footer__text-small {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.22;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #FCFCFC;
    max-width: 250px;
    margin-bottom: 14px;
}


.footer__text-big {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 1.22;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #FCFCFC;
    max-width: 250px;
}

.footer__list {
    display: flex;
    flex-direction: column;
    margin-bottom: 54px;
}


.footer__text-copywrite {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    letter-spacing: 0.02em;
    color: #7A7A7A;
    text-align: center;
    padding-top: 16px;
    padding-bottom: 62px;
    max-width: 100%;
    border-top: 1px solid #E1E1E1;
}

.footer__list-social {
    display: flex;
    align-items: center;
    justify-content: right;
    gap: 30px;
    margin-top: 42px;
    margin-bottom: 38px;
}

.footer__link-social {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background-color: #F8D24E;
}

.social__icon {
    fill: #84A178;
}

.footer__icon-social {
    fill: #AFB1B8;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);

    .footer__link-social:hover &,
    .footer__link-social:focus & {
        fill: #FCFCFC;
    }
}

.footer__block-tel {
    display: flex;
    justify-content: right;
}

.footer__link-tel {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    letter-spacing: 0.02em;
    color: #FFFFFF;
}

.footer__link-tel-span {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    letter-spacing: 0.02em;
    color: #131313;
    cursor: text;
}

@media screen and (min-width: 768px) {
    .footer__text-small {
        font-weight: 700;
        font-size: 26px;
        margin-bottom: 9px;
        max-width: 352px;
    }

    .footer__text-big {
        font-size: 48px;
        max-width: 340px;
    }

    .footer__link-tel {
        font-size: 14px;
    }

    .footer__link-tel-span {
        font-size: 14px;
    }

    .footer__list-social {
        margin-top: 68px;
        margin-bottom: 32px;
    }

    .footer__list {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 32px;
    }

    .footer__link-tel {
        font-size: 16px;
    }

    .footer__link-tel-span {
        font-size: 16px;
    }

    .footer__text-copywrite {
        font-size: 12px;
        line-height: 1.86;
        max-width: 100%;
        padding-top: 12px;
        padding-bottom: 11px;
    }

}

@media screen and (min-width: 1200px) {
    .footer__text-small {
        font-weight: 400;
        font-size: 30px;
        margin-bottom: 14px;
        max-width: 600px;
    }

    .footer__text-big {
        font-size: 58px;
        max-width: 600px;
    }

    .footer__link-tel {
        font-size: 16px;
    }


    .footer__link-tel-span {
        font-size: 16px;
    }

    .footer__list-social {
        margin-top: 12px;
        margin-bottom: 40px;
    }

    .footer__list {
        margin-bottom: 54px;
    }
}